.tooltipAnchor {
  position: relative;
  display: inline-block;
  cursor: default;
}

.tooltipAnchor .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: gray;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: var(--z-top);
  top: 150%;
  left: 65%;
  margin-left: -40px;
}

.tooltipAnchor .tooltiptext::after {
  content: ' ';
  position: absolute;
  bottom: 100%; /* At the top of the tooltip */
  left: 10%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent gray transparent;
}

.tooltipAnchor:hover .tooltiptext {
  visibility: visible;
}

.tooltipAnchor .tooltiptextabove {
  visibility: hidden;
  width: 120px;
  background-color: gray;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: var(--z-top);
  bottom: 150%;
  left: 50%;
  margin-left: -60px;
}

.tooltipAnchor .tooltiptextabove::after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: gray transparent transparent transparent;
}

.tooltipAnchor:hover .tooltiptextabove {
  visibility: visible;
}

/* The Modal (background) */
.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: var(--z-top);
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

/* Modal Content */
.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}

/* The Close Button */
.close {
  color: #aaaaaa;
  position: absolute;
  top: 10%;
  left: 90%;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

/* (?) Badge overrides bootstrap .badge */
.tooltipAnchor .badge {
  color: var(--clr-fg);
  background-color: var(--clr-system);
  vertical-align: text-top;
  padding: 0 0.25rem;
  margin-right: 2pt;
  display: inline-block;
  border-radius: 1rem;
  text-align: center;
  font-weight: var(--font-weight-bold);
}
